<template>
  <div id="match" ref="mainHeight">
    <div class="dealer_box user_box">
      <div class="card_box" v-for="(item, index) in dealer_arr" :key="index"
        :class="{ 'no_see': no_see_bool && index == dealer_arr.length - 1 }">
        <p v-if="no_see_bool && index == dealer_arr.length - 1"></p>
        <p v-else>{{ item }}</p>
      </div>

      <div class="current_score">
        <div>
          当前分数:
        </div>
        <div class="score_num">{{ dealer_score }}</div>
      </div>
    </div>

    <div class="operate_btn" v-if="operate_show">
      <div>
        <el-button class="click_btn" type="primary" plain @click="hit">HIT</el-button>
      </div>
      <div>
        <el-button class="click_btn" type="success" plain @click="stand">STAND</el-button>
      </div>
    </div>

    <div>
        <el-button class="click_btn" type="primary" plain @click="hit">HIT2</el-button>
      </div>
      <div>
        <el-button class="click_btn" type="success" plain @click="stand">STAND2</el-button>
      </div>

    <div class="player_box user_box">
    <div class="money_area">
        <div class="money">
          money:{{ balance }}
          <el-button type="primary" plain @click="add_money">add</el-button>
        </div>
        <div class="money">
          bet:{{ bet_num }}
          <el-button type="primary" plain @click="open">bet</el-button>
        </div>
      </div>
      <div class="card_area">
        <div class="card_box" v-for="(item, index) in player_arr" :key="index">
          <p>{{ item }}</p>
        </div>
        <div class="current_score">
          <div>
            当前分数:
          </div>
          <div class="score_num">{{ player_score }}</div>
        </div>
      </div>
    </div>
    <div class="bust_box" :class="{ 'dealer_win': dealer_win_bool, 'player_win': player_win_bool }">
      Bust!
    </div>
  </div>
</template>

<script>
import Vue from 'vue';
import Web3 from "web3";

export default {
  name: "match",
  
  data() {
    return {
      operate_show: false,
      no_see_bool: true,
      dealer_arr: [5, 10],
      player_arr: [10, 10],
      dealer_win_bool: false,
      player_win_bool: false,
      balance: 0,
      bet_num: 0,
      stand_num: 0,
      dealer_score: 0,
      player_score: 0,
      add_balance: 0,
      add_bet: 0,
      game_result: 0,
    };
  },
  methods: {

      async get_game() {
      //let result = await get_game().tuple
      //this.stand_num = this.stand_num + 1
      //下注金额
      //let bet = result[0]
      // 庄家隐藏牌
      //let dealer_hidden_card = result[1]
      // 庄家牌组
      //let dealer_card = result.slice(2, this.stand_num)
      //this.dealer_arr = [...dealer_card, ...dealer_hidden_card]
      // 玩家牌组
      //let player_card = await this.contract.methods.get_balance().call({ from: this.account })
      //this.player_arr = player_card
      // 是否结束
      //let game_result = result[result.length - 1]

      let game = await this.contract.methods.get_game().call({ from: this.account }).catch(err => {console.error('error get_game ' + err);})
      this.stand_num = this.stand_num + 1
      let bet = game.bet

      console.log("this game",this.account, game, bet)

      let dealer_hidden_card = game.dealer_hidden_card
      let dealer_card = game.dealer_card

      this.dealer_arr = [parseInt(dealer_card),parseInt(dealer_hidden_card)]
      console.log("dealer_arr get game",this.dealer_arr,parseInt(dealer_card),parseInt(dealer_hidden_card))
      let player_card = game.player_card
      this.player_arr = player_card
      console.log("player_arr get game",this.player_arr,parseInt(player_card))
      this.game_result =  game.is_finish

    },

    async get_result() {
      let result = await this.contract.methods.get_result().call({ from: this.account })
      if (result == 1) {
        // player win
        this.player_win_bool = true
        this.dealer_win_bool = false
      } else if (result == 2) {
        // dealer win
        this.player_win_bool = false
        this.dealer_win_bool = true
      } else {
        this.player_win_bool = false
        this.dealer_win_bool = false
      }
    },


    async add_money() {

      
      console.log("blance before charge", this.balance)
      this.$prompt(
        `Please enter how much you want to charge`,
        "message",
        {
          confirmButtonText: "confirm",
          showClose: false,
          showCancelButton: false,
          closeOnClickModal: false,
          closeOnPressEscape: false,
          closeOnHashChange: false,
        }
      )
        .then(({ value }) => {
          
          // 充值确认之后的调用
          this.add_balance = value
          console.log("add_balance0",this.add_balance)
          })

      const gasCharge = await this.contract.methods
                .charge_game_coins().estimateGas({ from: this.account }).catch(err => {console.err('error charge_game_coins ' + err);});
      console.log("gasCharge", gasCharge)

      await this.contract.methods.charge_game_coins().send({from: this.account, value: Web3.utils.toWei(String(this.add_balance)),gas: gasCharge})
      .catch(err => {console.err('error charge_game_coins ' + err);})
      console.log("add_balance00",this.add_balance)

      let has = await this.contract.methods.get_balance().call({ from: this.account })
      .catch(err => {
        console.err('error get_balance ' + err);
      })
      this.balance = has
      console.log("blance after charge", this.balance)

      let game = await this.contract.methods.get_game().call({ from: this.account }).catch(err => {console.error('error get_game ' + err);})

      console.log("this game0", game)
        





    },
    async open() {
      let max_num = this.balance;
      console.log("max_num", max_num, this.balance)


      console.log("bet_num",this.bet_num)

      let gasBet = await this.contract.methods
              .bet(this.bet_num).estimateGas({ from: this.account });
      console.log("gasBet", gasBet)

      this.$prompt(
        `Please enter your bet（you have ${max_num} now）`,
        "message",
        {
          confirmButtonText: "confirm",
          showClose: false,
          showCancelButton: false,
          closeOnClickModal: false,
          closeOnPressEscape: false,
          closeOnHashChange: false,
          inputValidator: (value) => {
            let RegExp = /(^[\-0-9][0-9]*(.[0-9]+)?)$/;
            return RegExp.test(value) && value <= max_num;
          },
          inputErrorMessage: "Invalid Number",
        }
      )
        .then(({ value }) => {
          //下注，使用bet函数
          this.bet_num = value
          console.log("betnum1",this.bet_num)
          this.contract.methods.bet(this.bet_num).send({from: this.account}).catch(err => {
        console.err('error bet ' + err);})
        })


        

        //this.get_game()

    },

    async stand() {

      await this.contract.methods.stand().send({from: this.account })
      .catch(err => {
        console.error('error stand ' + err);})
      
      let game = await this.contract.methods.get_game().call({ from: this.account }).catch(err => {console.error('error get_game ' + err);})

      console.log("this game after stand", game)

      await this.get_game()
      console.log("finishe this game",this.get_game)
      this.get_result()
      this.get_player_score()
      this.get_dealer_score()
    },
    async hit() {

      let game = await this.contract.methods.get_game().call({ from: this.account }).catch(err => {console.error('error get_game ' + err);})

      console.log("this game before hit", game)

      await this.contract.methods.hit().send({from: this.account })
      .catch(err => {
        console.error('error hit ' + err);
      })
      game = await this.contract.methods.get_game().call({ from: this.account }).catch(err => {console.error('error get_game ' + err);})

      console.log("this game after hit", game)


    },
    async get_player_score() {
      let player_score = await this.contract.methods.get_player_score().call({ from: this.account })
      .catch(err => {
        console.error('error get_player_score ' + err);
      })
      this.player_score = player_score
      console.log('get_player_score ',this.player_score)
    },
    async get_dealer_score() {
      let dealer_score = await this.contract.methods.get_dealer_score().call({ from: this.account })
      .catch(err => {
        console.error('error get_dealer_score ' + err);
      })
      this.dealer_score = dealer_score
      console.log('dealer_score ',this.dealer_score)
    }
  },
  async beforeMount() {

        // 获取当前余额，使用get_balance函数await this.contract.methods.get_balance().call()
    console.log("vuecontract", Vue.prototype.$contract)
    this.contract = Vue.prototype.$contract
    this.account = Vue.prototype.$account
    this.web3 = Vue.prototype.$web3
    console.log("contract", this.contract)

    let has = await this.contract.methods.get_balance().call({ from: this.account })
      .catch(err => {
        console.err('error get_balance ' + err);
      })
    
    console.log("balance0", has)
    this.balance = has
    console.log("balance00", this.balance)
    this.get_game()
    console.log("this player_arr",this.player_arr)
    //this.open()

  },
};
</script>

<style scoped="scoped" lang="scss">
#match {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;

  .user_box {
    height: 30vh;
    width: 100vw;
    padding: 0 3vw;
    display: flex;

    .card_box {
      height: 28vh;
      margin: 1vh;
      width: 10vw;
      border-radius: 10px;
      box-shadow: 3px 3px 7px rgba(0, 0, 0, 0.3);

      p {
        text-align: center;
        font: 100px/200px Georgia, Times New Roman, serif;
      }

      &.no_see {
        background: #ccc;
      }
    }

    .current_score {
      height: 10vh;
      margin-top: 10vh;
      width: 10vw;
      font-size: 2vw;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-content: center;

      .score_num {
        color: red;
        font-size: 3vw;
      }
    }
  }

  .player_box {
    display: flex;
    justify-content: space-between;

    .card_area {
      display: flex;
    }

    .money_area {
      width: 30vw;
      height: 28vh;
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      font-size: 3.5vw;
      border: 1px solid #ccc;

      div {
        height: 14vh;
        line-height: 14vh;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
      }

      .money {
        justify-content: space-around;
      }

      button {
        width: 8vw;
        height: 6vh;
        font-size: 2vw;
        line-height: 6vh;
        padding: 0;
      }
    }
  }

  .operate_btn {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    left: 0;
    background: black;
    opacity: 0.5;

    div {
      width: 35vw;

      .click_btn {
        width: 23vw;
        height: 5vw;
        font-size: 2vw;
      }
    }
  }

  .bust_box {
    font-size: 5vw;
    color: red;
    font-weight: 600;
    position: absolute;
    display: none;

    &.dealer_win {
      display: block;
      left: 80vw;
      top: 10vh;
    }

    &.player_win {
      display: block;
      left: 30vw;
      top: 80vh;
    }
  }
}
</style>
